<template lang="html">
     <div class="switch">
         <transition name="move">
           <div class="content" v-show="mainarea">
              <div class="content-header">
                  <h5></h5>
              </div>
              <div class="content-wrapper">
                    <div class="inputlist">
                      <span>:</span>
                      <input type="text" name=""  placeholder="" readonly="readonly"  v-model="station" >
                      <i class="iconfont icon-locate" @click="getPosition"></i>
                    </div>
                    <div class="inputlist">
                      <span>:</span>
                      <input type="text" name=""  placeholder=""  readonly="readonly" v-model="last">
                    </div>
                    <div class="inputlist">
                      <span>:</span>
                      <input type="text" name="" placeholder="" v-model="meter">
                    </div>
                    <div class="inputlist textarea">
                      <span>:</span>
                      <img class="ammeterImg" :src="imgUrl" v-if="ammeterImgIf"/>
                      <div class="picture" v-else>
                          <i class="iconfont icon-paizhao"  v-model="pic">
                            <input class="getImg" accept="image/*" type="file" @change="getImg"/>
                          </i>
                      </div>
                      <p class="uploadphoto" @click="getImgAgain"></p>
                    </div>
                    <div class="inputlist">
                      <span>:</span>
                      <input type="text" name="" placeholder="( kwh)" v-model="now" @input="countProportion">
                    </div>
                    <div class="inputlist">
                      <span>/:</span>
                      <input type="text" name=""  placeholder="/" readonly="readonly" v-model="percentage">
                    </div>
                    <div class="inputlist textarea1">
                      <span>:</span>
                      <textarea placeholder="" v-model="reason"></textarea >
                    </div>
              </div>
              <mt-actionsheet
                :actions="actions"
                v-model="sheetVisible">
              </mt-actionsheet>
              <!---->
              <!--<Map class="allmap" id="allmap" v-show="positionIf"></Map>-->
              <div class="allmap" id="allmap" v-show="positionIf"></div>
              <div class="content-footer">
                  <Button @skip="check" ></Button>
              </div>
         </div>
         </transition>
     </div>
</template>

<script>
import Header from '../Header.vue';
import Button from '../Button.vue';
import { Toast } from 'mint-ui';
import axios from 'axios';
import BMap from 'BMap';
axios.defaults.baseURL = "http://192.168.13.201:91/xiaogan/phone/"   // 

export default {
     data(){
       return{
          mainarea:false,
          selected:null,
          person:null,
          phone:null,
          sheetVisible:false,
          ammeterImgIf:false,
          positionIf:false,
          tijiao:true,
          options:['12','34','56'],
          dianbiao_id:"",
          x_code:"",
          y_code:"",
          code:"",
          name:"",
          bh:"",
          station:"",
          last:"",
          meter:"",
          now:"",
          percentage:"",
          reason:"",
          pic:"",
          imgUrl:"",
          actions:[
            {name:'',methods:'photoing'},
            {name:'',methods:'selecting'}
          ],
          type: 'tab',
          address_detail: null,
          center: {lng: 116.40387397, lat: 39.91488908}
       }
     },
     created(){
       this.name=this.$route.query.name;
       this.code=this.$route.query.code;
       this.bh=this.$route.query.bh;
     },
     methods:{
       Upload(){
         this.sheetVisible = true
       },
       getPosition(){
        let map = new BMap.Map('allmap');
        let point = new BMap.Point(this.center.lng, this.center.lat);
        this.x_code=point.lng;
        this.y_code=point.lat;
        map.centerAndZoom(point, 10);
        map.enableScrollWheelZoom(true);
        map.enableDoubleClickZoom(true);
        let self = this; 
        let geolocation = new BMap.Geolocation();
        geolocation.getCurrentPosition((r) => {
          let myGeo = new BMap.Geocoder();
          myGeo.getLocation(new BMap.Point(r.point.lng, r.point.lat), function(result){ 
            if (result){
              //
              let gc = new BMap.Geocoder();
              let pt = new BMap.Point(r.point.lng, r.point.lat);
              let lngLat=r.point.lng+","+r.point.lat;
              axios.get("code",{
                  params:{
                    // x_code: self.x_code,
                    // y_code: self.y_code,
                    x_code: '120.25155029369263',
                    y_code: '30.16567767483513',
                    code:self.code,
                    name: self.name,
                    bh:self.bh
                  }
              }).then(res=>{
                if(res.data.code==-1){
                  Toast({
                    message: '',
                    duration:1000,
                  });
                  this.$router.go(-1)
                }else if(res.data.code==0){
                  self.station=res.data.station;
                  self.last=res.data.last;
                  self.dianbiao_id=res.data.dianbiao_id;
                };
              })
              
            }
          },{ enableHighAccuracy: true });
          // 
          // if (r.point) {
          //   let markers = new BMap.Marker(r.point);
          //   map.addOverlay(markers)
          //   map.panTo(r.point)
          //   map.centerAndZoom(r.point, 16);
          //   self.positionIf=true;
          //   setTimeout(function(){
          //     self.positionIf=false;
          //   },1000);
          // }
        })
       },
       check(){
          let formData = new FormData();
          formData.append("station",this.station);
          formData.append("last",this.last);
          formData.append("meter",this.meter);
          formData.append("now",this.now);
          formData.append("percentage",this.percentage);
          formData.append("reason",this.reason);
          formData.append("pic",this.pic);
          // formData.append("x_code",this.x_code);
          // formData.append("y_code",this.y_code);
          formData.append("x_code",'120.25155029369263');
          formData.append("y_code",'30.16567767483513');
          formData.append("dianbiao_id",this.dianbiao_id);
          let config = {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          };
          if(this.reason){
            if(this.station&&this.last&&this.meter&&this.now&&this.percentage&&this.pic&&this.x_code&&this.y_code&&this.dianbiao_id){
              axios.post("reading",formData,config).then(res=>{
                if(res.data.code==-1){
                  Toast({
                    message: '',
                    duration:1000,
                  });
                }else if(res.data.code==0){
                  Toast({
                    message: '',
                    duration:1000,
                  });
                  this.$router.go(0)
                  
                };
              })
            }else {
                Toast({
                  message: '',
                  duration:1000,
                });
            }
          }else if(this.percentage>0.7){
            Toast({
                  message: '70% ',
                  duration:1000,
            });
          }else {
            if(this.station&&this.last&&this.meter&&this.now&&this.percentage&&this.pic){
              axios.post("reading",formData,config).then(res=>{
                if(res.data.code==-1){
                  Toast({
                    message: '',
                    duration:1000,
                  });
                }else if(res.data.code==0){
                  Toast({
                    message: '',
                    duration:1000,
                  });
                  this.$router.go(0)
                  
                };
              })
            }else {
                Toast({
                  message: '',
                  duration:1000,
                });
            }
          }
       },
      
       getImg (e) {
        let file = e.target.files[0];
        let formData = new FormData();
        formData.append("files",file);
        let config = {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
        };
        let that=this;
        axios.post("upload",formData,config).then(res=>{
          if(res.data.code==-1){
            Toast({
              message: '',
              duration:500,
            });
          }else if(res.data.code==0){
            that.imgUrl="http://"+res.data.url;
            that.pic=that.imgUrl;
            that.ammeterImgIf=true;
          };
        })
      },
      getImgAgain(){
          // this.ammeterImgIf=false;
          this.getImg();
      },
      countProportion(){
        
        this.percentage=this.last/this.now;
        
       
      }
     },
     mounted(){
        this.mainarea = true;
        this.getPosition();
     },
     components:{
         Header,
         Button
     }
}
</script>

<style lang="scss" scoped>

     .switch{
         margin-top: -30px;
       .content{
         padding-left: 10px;
         padding-right: 10px;
         &.move-enter-active,
         &.move-leave-active {
             transition: all 0.2s;
             opacity: 0.8;
         }
         &.move-enter,
         &.move-leave-active {
             transition: all 0.2s;
             transform: translate3d(100%, 0, 0);
             opacity: 0.8;
         }
         .content-header{
           display: flex;
           h5{
             color:#101010;
             font-weight: 700;
             font-size: 14px;
             margin-top: -10px;

           }
         }
         
         .content-wrapper{
           margin-top: 12px;
           .inputlist{
             position: relative;
              display: flex;
              align-items: center;
              margin: 3px 0;
              height: 46px;
              background-color: #fff;
              border-radius:2px;
              .icon-locate{
                font-size: 16px;
                position: absolute;
                right: 12px;
                color: #b5b5b5;
              }
              .icon-up{
                font-size: 3px;
                position: absolute;
                right: 12px;
                top:14px;
                color: #b5b5b5;

              }
              .icon-down{
                font-size: 3px;
                position: absolute;
                top:22px;
                right: 12px;
                color: #b5b5b5;
              }
              select{
                width: 70%;
                height: 24px;
                outline: none;
                border-radius: 5px;
                margin-left: 5px;
                padding-left: 5px;
                font-size: 12px;
                margin-right: 8px;
                border:1px solid #f1f1f1;

                background-color:transparent;




              }
              input{
                width: 70%;
                height: 24px;
                outline: none;
                border:1px solid #f1f1f1;
                border-radius: 5px;
                margin-left: 5px;
                padding-left: 5px;
                font-size: 12px;
                margin-right: 8px;

                text-indent: 0;
                background: transparent;

                resize:none;

                -webkit-appearance:none;  /* */
                line-height: normal;   //





              }
              span{
                width: 25%;
                color: #6b6b6b;
                margin-left: 5px;
                margin-right: 10px;
                padding-top: 8px;
                font-size: 12px;
                font-weight: 400;
                white-space:pre-wrap;
                word-wrap : break-word ;
                overflow: hidden ;
              }
           }
           .textarea{
             display: flex;
             justify-content: flex-start;
             height: 90px;
             .ammeterImg{
                  width:60px;
                  height:60px;
              }
             .picture{
                width: 60px;
                height: 60px;
                background-color: #ececec;
                .getImg{
                    opacity:0;
                }
                .icon-paizhao:before{
                  position: relative;
                  top:20px;
                  
                }
             }
             .uploadphoto{
               font-size: 12px;
               margin-left: 10px;
               margin-top: 40px;
               color: #c5c5c5;
             }
             span{
               margin-top:-30px;
             }
           }
           .textarea1{
             display: flex;
             height: 68px;
             textarea{
               border:1px solid #f1f1f1;
               margin-right: 12px;
               width: 70%;
               height: 40px;
               outline: none;
             }
           }
         }
        //  
         .allmap{
            width:90%;
            height: 200px;
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            margin:auto;
         }
         .content-footer{
           margin: 20px 0;
           .button{
              font-size: 14px;
              font-weight: 400;
              color:#ffffff;
              text-align: center;
              background: #8ed959;
              width: 80%;
              margin:0 auto;
              box-sizing: border-box;
              padding:0 12px;
              height: 35px;
              line-height: 35px;
              margin-top: 15px;
              border-radius:5px;
            }

         }
       }
     }
</style>



